.BobaRanking {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: auto;
    grid-gap: 10px;
    padding: 20px;
    background-color: #f2f2f2;
    border: 2px solid #d9d9d9;
    border-radius: 10px;
  }
  
  .BobaRanking-header {
    grid-column: 1 / -1;
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .BobaRanking-cell {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    border: 2px solid #d9d9d9;
    border-radius: 5px;
    padding: 10px;
  }
  
  .BobaRanking-cell:hover {
    background-color: #f2f2f2;
    cursor: pointer;
  }
  
  .BobaRanking-cell img {
    width: 100%;
    height: auto;
    margin-bottom: 10px;
  }
  
  .BobaRanking-cell h2 {
    font-size: 18px;
    font-weight: bold;
    margin: 0;
    margin-bottom: 5px;
  }
  
  .BobaRanking-cell p {
    font-size: 14px;
    margin: 0;
  }
  